.timeline-tab{max-width:1160px;margin:0 auto;padding:24px 16px 28px}.timeline-tab__header{margin-bottom:6px}.timeline-tab__header h2{margin:0;font-size:32px;font-weight:800;line-height:1.08;color:#f8fafc;font-family:"Lora",Georgia,serif;display:flex;align-items:center;gap:10px}.timeline-tab__header .timeline-tab__header-icon{font-size:28px}.timeline-tab__header-sub{margin:6px 0 0;font-size:13px;color:#94a3b8;letter-spacing:.01em}.timeline-tab__header-sub strong{color:#e2e8f0}.timeline-tab__legend{display:flex;flex-wrap:wrap;gap:8px;margin-bottom:16px}.timeline-tab__legend-chip{display:inline-flex;align-items:center;gap:5px;font-size:11px;color:#94a3b8;background:rgba(15,23,42,.6);border:1px solid rgba(148,163,184,.18);border-radius:999px;padding:3px 10px 3px 6px}.timeline-tab__legend-dot{width:8px;height:8px;border-radius:999px;flex-shrink:0}.timeline-tab__surface{border:1px solid rgba(148,163,184,.18);border-radius:16px;background:rgba(15,23,42,.55);backdrop-filter:blur(8px);padding:24px;overflow:hidden}.timeline-tab__layout{display:flex;gap:28px;align-items:flex-start}.timeline-tab__rail-col{position:relative;flex:0 0 280px;min-height:680px;padding-bottom:48px}.timeline-tab__callout-col{flex:1 1 0;position:sticky;top:20px;align-self:flex-start}.timeline-tab__rail{position:absolute;left:115px;width:3px;border-radius:999px;background:linear-gradient(180deg, rgba(248, 113, 113, 0.6) 0%, rgba(250, 204, 21, 0.6) 12%, rgba(251, 191, 36, 0.6) 22%, rgba(251, 146, 60, 0.6) 38%, rgba(52, 211, 153, 0.6) 54%, rgba(96, 165, 250, 0.6) 72%, rgba(167, 139, 250, 0.8) 100%)}.timeline-tab__event{position:absolute;left:0;right:0;height:0}.timeline-tab__year{position:absolute;right:calc(100% - 102px);transform:translateY(-50%);text-align:right;white-space:nowrap;font-size:12px;font-weight:700;color:#93c5fd;font-variant-numeric:tabular-nums;pointer-events:none;background:rgba(15,23,42,.7);padding:1px 6px;border-radius:4px}.timeline-tab__dot{position:absolute;left:115px;transform:translate(-50%, -50%);width:16px;height:16px;border-radius:999px;border:2.5px solid;background:#0f172a;cursor:pointer;transition:all .22s cubic-bezier(0.4, 0, 0.2, 1);z-index:2}.timeline-tab__dot:hover{transform:translate(-50%, -50%) scale(1.25)}.timeline-tab__dot.is-selected{transform:translate(-50%, -50%) scale(1.35);animation:dot-pulse 2s ease-in-out infinite}@keyframes dot-pulse{0%,100%{opacity:1}50%{opacity:.7}}.timeline-tab__tick{position:absolute;left:108px;transform:translateY(-50%);width:14px;height:2px;border-radius:1px}.timeline-tab__title{position:absolute;left:134px;transform:translateY(-50%);display:flex;flex-direction:column;gap:1px;max-width:140px;font-size:12px;font-weight:600;line-height:1.3;color:#f1f5f9;pointer-events:none}.timeline-tab__title.is-muted{color:#94a3b8;font-weight:400}.timeline-tab__era-tag{font-size:9px;font-weight:600;text-transform:uppercase;letter-spacing:.06em;opacity:.8}.timeline-tab__callout{border:1px solid rgba(148,163,184,.22);border-top:3px solid;border-radius:14px;background:linear-gradient(168deg, rgba(15, 23, 42, 0.97) 0%, rgba(15, 23, 42, 0.86) 100%);padding:20px 22px;display:grid;gap:14px;box-shadow:0 16px 40px rgba(2,6,23,.55),inset 0 1px 0 hsla(0,0%,100%,.04);margin-top:38px;animation:callout-enter .3s cubic-bezier(0.4, 0, 0.2, 1)}@keyframes callout-enter{from{opacity:0;transform:translateY(8px)}to{opacity:1;transform:translateY(0)}}.timeline-tab__callout-head{display:grid;gap:8px}.timeline-tab__callout-head h3{margin:0;font-size:22px;font-weight:700;color:#f8fafc;font-family:"Lora",Georgia,serif;line-height:1.15}.timeline-tab__meta-row{display:flex;flex-wrap:wrap;gap:6px}.timeline-tab__pill{font-size:11px;font-weight:600;border:1px solid rgba(148,163,184,.28);border-radius:999px;padding:3px 10px;color:#cbd5e1;background:rgba(30,41,59,.7)}.timeline-tab__pill.is-year{font-variant-numeric:tabular-nums;font-weight:700;color:#93c5fd;border-color:rgba(96,165,250,.35);background:rgba(96,165,250,.1)}.timeline-tab__summary{font-size:14px;color:#dbeafe;line-height:1.55;margin:0}.timeline-tab__callout-sections{display:grid;gap:16px}.timeline-tab__section{position:relative;padding-left:14px}.timeline-tab__section::before{content:"";position:absolute;left:0;top:2px;bottom:2px;width:3px;border-radius:2px;background:rgba(148,163,184,.18)}.timeline-tab__section h4{margin:0 0 4px;font-size:13px;font-weight:700;color:#e2e8f0;text-transform:uppercase;letter-spacing:.04em}.timeline-tab__section p{margin:0;font-size:13px;color:#cbd5e1;line-height:1.5}.timeline-tab__section ul{margin:0;padding-left:16px;font-size:13px;color:#cbd5e1;line-height:1.6;display:grid;gap:2px}.timeline-tab__section li::marker{color:rgba(148,163,184,.5)}.timeline-tab__faction-row{display:flex;flex-wrap:wrap;gap:5px}.timeline-tab__faction-chip{font-size:11px;color:#94a3b8;background:rgba(30,41,59,.8);border:1px solid rgba(148,163,184,.2);border-radius:6px;padding:3px 8px}.timeline-tab__source-list{list-style:none !important;padding-left:0 !important}.timeline-tab__source-list li{position:relative;padding-left:16px;font-style:italic;color:#94a3b8;font-size:12px}.timeline-tab__source-list li::before{content:"ðŸ“œ";position:absolute;left:0;font-size:11px;font-style:normal}.timeline-tab__tag-row{display:flex;flex-wrap:wrap;gap:6px;padding-top:6px;border-top:1px solid rgba(148,163,184,.12)}.timeline-tab__tag{font-size:11px;color:#64748b;font-weight:500}@media(max-width: 960px){.timeline-tab__layout{flex-direction:column}.timeline-tab__rail-col{flex:none;width:100%}.timeline-tab__callout-col{position:static}.timeline-tab__callout{margin-top:16px}}@media(max-width: 600px){.timeline-tab{padding:14px 10px}.timeline-tab__header h2{font-size:24px}.timeline-tab__surface{padding:14px}.timeline-tab__rail-col{flex:0 0 220px}.timeline-tab__title{max-width:90px}}